@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", verdana, sans-serif;
  font-size: 1em;
  line-height: 1em;
  font-weight: normal;
  text-decoration: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

ul,
ol {
  list-style: none; }

button,
input {
  background: transparent;
  border: none;
  outline: none;
  box-shadow: none;
  cursor: pointer; }

.bttn {
  padding: 15px 30px;
  font-size: 1.2em;
  font-weight: 500;
  color: #404751;
  border: 1px solid #CFD1D3;
  cursor: pointer; }
  .bttn:hover {
    background-color: #CFD1D3; }

.inputField {
  padding: 20px 40px; }
  .inputField label {
    display: block;
    margin-bottom: 10px;
    font-size: 1.2em;
    font-weight: 700;
    color: #404751; }
  .inputField input {
    width: 100%;
    padding: 10px 20px;
    font-size: 1em;
    color: #404751;
    border: 1px solid #CFD1D3; }
  .inputField button {
    padding: 15px 30px;
    font-size: 1.2em;
    font-weight: 700;
    color: #404751;
    border: 1px solid #CFD1D3;
    cursor: pointer; }
    .inputField button:hover {
      background-color: #CFD1D3; }

.Component {
  width: 100%;
  height: 100%; }
  .Component main {
    padding: 40px; }

.Login {
  width: 100%;
  height: 100%;
  padding: 40px 20px; }
  .Login header {
    padding: 40px 0; }
    .Login header h1 {
      font-size: 2em;
      font-weight: 700;
      color: #404751;
      text-align: center; }
  .Login main {
    padding: 20px 0; }

.Message main {
  text-align: center; }
  .Message main .messageBox {
    padding: 40px 0;
    text-align: center; }
    .Message main .messageBox h2 {
      font-size: 1.6em;
      font-weight: 600; }

nav {
  width: 100%;
  height: 50px;
  padding: 5px 20px;
  display: -webkit-flex;
  display: flex;
  position: relative;
  background-color: #404751; }
  nav .navInner {
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    line-height: 50px;
    position: relative; }
    nav .navInner .navLeft {
      position: absolute;
      top: 0;
      left: 0; }
      nav .navInner .navLeft button {
        color: #fff; }
        nav .navInner .navLeft button:hover {
          color: #e800e9; }
    nav .navInner .navMiddle {
      margin: auto; }
      nav .navInner .navMiddle h2 {
        color: #fff; }
    nav .navInner .navRight {
      position: absolute;
      top: 0;
      right: 0; }
    nav .navInner > div {
      height: 100%;
      display: -webkit-flex;
      display: flex; }
      nav .navInner > div h1,
      nav .navInner > div h2,
      nav .navInner > div span {
        margin: auto 0;
        color: #fff; }

main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center; }
  main .question {
    font-size: 30px;
    margin-bottom: 10px; }
  main .category {
    font-size: 20px; }

.Error {
  width: 100%;
  height: 50px;
  padding: 5px 20px;
  display: -webkit-flex;
  display: flex;
  position: fixed;
  bottom: 0;
  background-color: #404751;
  -webkit-justify-content: center;
          justify-content: center; }
  .Error h2 {
    font-size: 0.9em;
    color: #fff;
    white-space: nowrap;
    line-height: 40px; }
  .Error button {
    width: 1.8em;
    height: 1.8em;
    margin: auto 0 auto 10px;
    border-radius: 100%;
    background-color: transparent;
    border: 1px solid #CFD1D3;
    cursor: pointer; }
    .Error button svg {
      width: 40%;
      height: 40%;
      fill: #fff; }

